@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
body {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  color: #444444;
  line-height: 18px;
  background-color: #ffffff;
  margin: 0;
  min-width: 320px;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

@media (min-width: 768px) {
  body {
    font-size: 16px;
    line-height: 24px;
  }
}
.container {
  min-width: 320px;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    margin: 0 auto;
    padding: 0 40px;
    max-width: 600px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-scope__list {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.main-scope {
  position: relative;
  padding-top: 60px;
}

.main-scope__item {
  display: flex;
  align-items: center;
  font-size: 24px;
}
.main-scope__item.important {
  background-color: rgba(254, 187, 198, 0.841);
}
.main-scope__item.important svg {
  fill: rgba(255, 17, 0, 0.883);
}

.main-scope__checkbox {
  width: 30px;
  height: 30px;
  margin-right: 15px;
  cursor: pointer;
}

.main-scope__desc.done {
  text-decoration: line-through;
}

.main-scope__btn {
  background-color: transparent;
  border: none;
  margin-left: auto;
  cursor: pointer;
}
.main-scope__btn:disabled {
  opacity: 0.3;
}

.main-scope__desc {
  border-bottom: 1px solid black;
  flex-grow: 1;
}

.main-scope__del-btn.hidden {
  display: none;
}
.main-scope__del-btn:disabled {
  opacity: 0.7;
}
.main-scope__del-btn:disabled:hover + .main-scope__tooltip {
  display: block;
}

.main-scope__tooltip {
  position: absolute;
  display: none;
  top: 50%;
  left: calc(100% + 22px);
  width: 200px;
  background-color: bisque;
  padding: 10px;
  border-radius: 5px;
  transform: translateY(-50%);
}
.main-scope__tooltip::after {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: bisque;
  transform: rotate(45deg) translateY(-50%);
  top: 50%;
  left: -12px;
}
.main-scope__tooltip p {
  margin: 0;
}

.main-scope__del-wrapper {
  position: absolute;
  top: 10px;
  left: 0;
}

.notification {
  position: fixed;
  top: 20px;
  right: 20px;
  font-size: 20px;
  color: #f2f2f2;
  background-color: #000000;
  padding: 30px;
  border-radius: 10px;
  transform: scale(0);
  transform-origin: 100% 0;
  transition: transform 0.3s;
}
.notification.visible {
  transform: scale(1);
  transition: transform 0.3s;
}

.title {
  text-align: center;
}

.form ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}
.form li {
  margin-bottom: 12px;
}

.form__label {
  display: block;
}

.form__checkbox + .form__label {
  display: inline-block;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: #ffffff;
  z-index: 150;
}

.modal__wrapper {
  padding: 50px;
  position: relative;
  margin: auto;
  background-color: #ffffff;
  box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.3);
  inset: auto;
  z-index: 5;
}

.modal--close {
  display: none;
}

.modal__overlay {
  position: absolute;
  z-index: 3;
  inset: 0;
}

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.add {
  text-align: center;
}